import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TABS_DEMO_ROUTES } from './cpo-crud/routes';
import { CpoCrudComponent } from './cpo-crud/cpo-crud.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : CpoCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    },
      // {
      //   path: 'chargingStation',
      //   // loadChildren: () => import('./configuration/charger-station/charging-station.module').then(m => m.ChargingStationModule),
      //   loadChildren: () => import('../charger-station/charging-station.module').then(m => m.ChargingStationModule),
      //   data: { title: 'Charging Station', titleI18n: 'chargingStation' },
      // },
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class CpoRoutingModule{}